import Backbone from 'backbone';

export default Backbone.View.extend({
  tagName: 'style',

  initialize(o = {}) {
    this.config = o.config || {};
    const { model } = this;
    this.listenTo(model, 'change', this.render);
    this.listenTo(model, 'destroy remove', this.remove);
    this.listenTo(model.get('selectors'), 'change', this.render);
  },

  render() {
    const { model, el } = this;
    const important = model.get('important');
    // const important = true;
    // replace twice to avoid !important!important
    // let css = model
    //   .toCSS({ important })
    //   .replace(/!important !important/g, '!important');
    el.innerHTML = model.toCSS({ important });
    // el.innerHTML = css;
    return this;
  }
});
