import { isArray } from 'underscore';
import { getTableMatrix, CELL_TYPE, isHolderCell } from 'utils/Table';

import Backbone from 'backbone';

var $ = Backbone.$;

export default {
  run(ed, sender, opts = {}) {
    let components = opts.component || ed.getSelectedAll();
    components = isArray(components) ? [...components] : [components];

    // component
    var component = this.em.getSelected();
    if (!component) {
      return;
    }

    // matrix
    var view = component.view;
    let $cell = $(view.el);
    var $table = $cell.closest('table');
    var matrix = getTableMatrix($table);

    // ajust the spans
    var el = view.el;
    var $el = $(el);
    var $row = $el.closest('tr');
    var row_index = $row.index();
    var matrix_cells = matrix[row_index];
    for (var i = 0; i < matrix_cells.length; i++) {
      var cell_info = matrix_cells[i];
      // pre deal the cell info
      if (cell_info.type == CELL_TYPE.COL_SPAN) {
        // do nothing
        continue;
      } else if (cell_info.type == CELL_TYPE.ROW_SPAN) {
        let source_cell = cell_info.cell;
        source_cell.rowSpan = parseInt(source_cell.rowSpan) + 1;
      } else if (cell_info == CELL_TYPE.COL_ROW_SPAN) {
        let source_cell = cell_info.cell;
        source_cell.rowSpan = parseInt(source_cell.rowSpan) + 1;
      } else if (
        cell_info.type == CELL_TYPE.CELL &&
        cell_info.rowSpan &&
        cell_info.rowSpan > 1
      ) {
        let source_cell = cell_info.cell;
        source_cell.rowSpan = parseInt(source_cell.rowSpan) + 1;
        // change the cell to holder
        cell_info.type = CELL_TYPE.ROW_SPAN;
      }
    }

    // append the row and the cells
    let cells = [];
    for (let i = 0; i < matrix_cells.length; i++) {
      let cell_info = matrix_cells[i];
      if (cell_info.type == CELL_TYPE.CELL) {
        let cell = {
          type: 'cell',
          attributes: {}
        };
        if (cell_info.colSpan && cell_info.colSpan > 1) {
          cell.attributes.colSpan = cell_info.colSpan;
        }
        cells.push(cell);
      }
    }

    // find the tbody
    let $table_body_model = $table.find('tbody');
    if (!$table_body_model.length) {
      $table_body_model = $table;
    }
    let $body_model = $table_body_model.data('model');
    // as the chidren will trigger add mesage, so we need to prevent it
    let row_model = $body_model.append(
      {
        type: 'row',
        components: cells
      },
      {
        at: row_index + 1
      }
    );
    ed.trigger('component:add_row', row_model);
  }
};
