import { isArray } from 'underscore';
import {
  getTableMatrix,
  CELL_TYPE,
  isHolderCell,
  getRealIndex
} from 'utils/Table';
import { parseAttributes } from './../../parser/model/BrowserParserHtml';

import Backbone from 'backbone';

var $ = Backbone.$;

export default {
  run(ed, sender, opts = {}) {
    let components = opts.component || ed.getSelectedAll();
    components = isArray(components) ? [...components] : [components];
    if (components.length > 1) {
      return;
    }

    // component
    var component = this.em.getSelected();
    if (!component) {
      return;
    }

    // matrix
    let view = component.view;
    let $td = $(view.el);
    let $tr = $td.closest('tr');
    let row_index = $tr.index();
    let $table = $td.closest('table');
    let $trs = $table.find('tr');
    let matrix = getTableMatrix($table);

    let current_row = matrix[row_index];
    let virtual_cell_index = -1;
    // find the virtual cell index
    for (let i = 0; i < current_row.length; i++) {
      let tmp_cell_info = current_row[i];
      if (tmp_cell_info.cell == $td[0]) {
        virtual_cell_index = i;
        break;
      }
    }

    // update the matrix
    for (let row_index = 0; row_index < matrix.length; row_index++) {
      let $row = $($trs[row_index]);
      let row_model = $row.data('model');
      let matrix_row = matrix[row_index];
      let cell_info = matrix_row[virtual_cell_index];
      let related_cell = cell_info.cell;
      let real_cell_index = getRealIndex(matrix_row, virtual_cell_index);

      if (cell_info.type === CELL_TYPE.COL_SPAN) {
        let colspan = cell_info.colspan;
        related_cell.colspan = colspan + 1;
      } else if (
        cell_info.type === CELL_TYPE.ROW_SPAN &&
        cell_info.row_index === cell_info.related_cell_row_index
      ) {
        // add the cell
        let models = row_model.append(
          {
            tagName: 'cell',
            attributes: {
              rowspan: cell_info.rowspan
            }
          },
          {
            at: real_cell_index + 1
          }
        );
        ed.trigger('component:add_cells', models);
      } else if (
        cell_info.type === CELL_TYPE.COL_ROW_SPAN &&
        cell_info.row_index === cell_info.related_cell_row_index
      ) {
        // add the colspan，as we add a column
        related_cell.colspan = related_cell.colspan + 1;
        // keep the rowspan
      } else if (cell_info.type == CELL_TYPE.CELL) {
        // if the cell has colspan, we must remove the colspan
        if (cell_info.rowspan > 1 && cell_info.colspan > 1) {
          // just keep the columnspan
          let models = row_model.append(
            {
              type: 'cell',
              attributes: {
                rowSpan: cell_info.rowSpan
              }
            },
            {
              at: real_cell_index + 1
            }
          );
          ed.trigger('component:add_cells', models);
        } else {
          let models = row_model.append(
            {
              type: 'cell'
            },
            {
              at: real_cell_index + 1
            }
          );
          ed.trigger('component:add_cells', models);
        }
      }
    }
  }
};
